import { useState } from 'react'

export default function SusComponent() {
    const [name, setName] = useState("")

    return (
        <>
            <input autoComplete="off" type="text" name="name" placeholder="Name" onChange={e => setName(sussify(e.target.value))} />
            <label>{name}</label>
        </>
    )
}

function sussify(name) {
    if (name === "") {
        return ""
    }
    if (strEndsWith(name, ["d", "k", "t", "y", "s", "h", "l"])) {
        name = name + "us";
        return name
    }
    let match = name.match(/[aeiou]([^aeiou]*?)(?= |$)/g)
    if (match == null) {
        name = name + "us"
    } else {
        name = name.replace(/[aeiou]([^aeiou]*?)(?= |$)/g, "us")
    }
    return name
}

function strEndsWith(str, ends) {
    return ends.some(function (ends) {
        return str.endsWith(ends);
    });
};